import React from 'react';
import Card from './Card';

const CardList = ({robots}) => {
    if(!robots.length) {
        return <h1 className='pa5'>no robot found</h1>
    } else {
        return (
            <div>
                {robots.map(robot => 
                    <Card 
                        key={robot.id} 
                        id={robot.id} 
                        name={robot.name} 
                        email={robot.email} 
                        username={robot.username} 
                    />
                )}
            </div>
        );
    }
}

export default CardList;